$grdNo: 12;
$bgc: background-color;
// *****************
$f :font;
$z :size;
$brd: 1px groove $rclr;
// ************************
$mytheme: nature;
@mixin theme($theme: Gray) {
    background: $theme;
    box-shadow: 0 0 1px rgba($theme, .25);
    color: #fff;
}
.theme {
  @if $mytheme == fire { 
      @include theme($theme: Red);
  }@else if $mytheme == sea {
      @include theme($theme: Blue);
  }@else if $mytheme == nature {
      @include theme($theme: Green);
  }@else{
      @include theme;
  }
}
@for $i from 1 through 7 {
.class#{$i}{
   	font-size: $sz+$i;
    background: darken($sclr, $i/8);
    theme-color: scale($sclr, $i/8);
    background-color: saturate($sclr, $i/8);
    color: desaturate($sclr, $i/8);
    border-top-left-radius:adjust-hue($sclr, 100*$i) ;
    border-top-right-radius:invert($sclr, $weight: $i*10%);
    border-right:lighten($sclr, 10*$i);
    border-top:mix($sclr, $i*$sclr, $weight: 50%);
    border-bottom-left-radius:complement($sclr/$i) ;
    border-bottom-right-radius:grayscale($sclr/$i) ;
    border-bottom:opacify($sclr, $i/10);
    border-left:fade-in($sclr, $i/8) ;
    border: 1px groove opacify(rgba($sclr, $i/10), $i* 0.1);
    	}
    }
